(function() {
  'use strict';

  angular
    .module('icpComp', ['ngAnimate', 'ngCookies', 'ngSanitize', 'ngMessages', 'ngResource', 'ui.router', 'ui.bootstrap', 'toastr']);

})();

(function() {

     angular.module('icpComp')
          .component('page2', {
               templateUrl: 'app/pages/page2/page2.html',
               controllerAs: 'vm',
               controller: Page2Controller
          });

     function Page2Controller($log) {
          var vm = this;
          vm.message = 'Hello from the page2 component!';
          $log.log("sup.");
     }

})();
(function() {

   angular.module('icpComp')
      .component('page1', {
         templateUrl: 'app/pages/page1/page1.html',
         controllerAs: 'vm',
         controller: Page1Controller
      });

   function Page1Controller($log) {
      var vm = this;
      vm.message = 'Hello from the page 1 component!';
      $log.log("hello y'all. I'm page 1!");
   }

})();
(function() {

     angular.module('icpComp')
          .component('homePage', {
               templateUrl: 'app/pages/home/home.html',
               controllerAs: 'vm',
               controller: HomeController
          });

     function HomeController($log) {
          var vm = this;
          vm.message = 'Hello from home comopnent\'s controller!';
          $log.log("Started Home Component!");
     }

})();
(function() {
  'use strict';

  angular
    .module('icpComp')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function () {
   'use strict';

   angular
      .module('icpComp')
      .config(routerConfig);

   /** @ngInject */
   function routerConfig($stateProvider, $urlRouterProvider) {
      $stateProvider
         .state('home', {
            url: '/',
            template: "<home-page></home-page>"
         })
         .state('page1', {
         url: '/page1',
         template: "<page1></page1>"
         })
         .state('page2', {
            url: '/page2',
            template: "<page2></page2>"
         });


      $urlRouterProvider.otherwise('/');
   }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('icpComp')
    .constant('malarkey', malarkey)
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('icpComp')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("icpComp").run(["$templateCache", function($templateCache) {$templateCache.put("app/pages/home/home.html","<div class=home-page><div ui-sref=second class=home-header>HOME</div><div><span ng-bind=vm.message></span></div><br><button ui-sref=page1>Page 1</button> <button ui-sref=page2>Page 2</button></div>");
$templateCache.put("app/pages/page1/page1.html","<div class=page1-page><h1>Page 1</h1><div ng-bind=vm.message></div><div><button ui-sref=home>Home</button> <button ui-sref=page2>Page 2</button></div></div>");
$templateCache.put("app/pages/page2/page2.html","<div class=page2-page><h1>Page 2</h1><div ng-bind=vm.message></div><div><button ui-sref=home>Home</button> <button ui-sref=page1>Page 1</button></div></div>");}]);